import { cssProps } from '@oracle/oraclejet-cssprops';
import { VNode } from 'preact';
import { MessageVariant } from './Message.types';

const messageCloseButtonStyles = {
  banner: cssProps`${{
    marginInlineStart: '0.25rem',
    marginInlineEnd: '-0.5rem',
    marginTop: '-0.5rem',
    marginBottom: '-0.5rem'
  }}`,
  inline: undefined
};

/**
 * Props for the MessageCloseButton component
 */
type Props = {
  /**
   * The callback function to be called when the close icon is clicked
   */
  onAction: (event?: Event) => void;

  /**
   * Button renderer
   */
  buttonRenderer?: (
    title: string,
    onAction: (event?: Event) => void,
    variant?: MessageVariant
  ) => VNode;

  /**
   * Style variant
   */
  variant?: MessageVariant;

  /**
   * Close button title
   */
  title?: string;
};

/**
 * A Component for rendering the message close button
 */
function MessageCloseButton({
  onAction,
  buttonRenderer,
  title = 'Close',
  variant = 'banner'
}: Props) {
  const renderedButton = buttonRenderer ? (
    buttonRenderer(title, onAction, variant)
  ) : (
    // we should ultimately be able to create an oj-button (or rather its preact
    // equivalent) here, but for now just create a regular HTML button if a renderer
    // is not passed in from the core JET layer
    <button aria-label={title} onClick={onAction} title={title}>
      X
    </button>
  );
  const classes = `oj-c-message${variant}-close-icon ${messageCloseButtonStyles[variant]}`;

  // Otherwise, render the close icon
  return <div class={classes}>{renderedButton}</div>;
}

export { MessageCloseButton };
